import '../App.css';
import React, { Component } from 'react';
import axios from 'axios';

class RegisterForm extends Component {
    constructor(props) {
        super(props)
        this.state = {
            name: "kalana",
            password: "pass",
            email : props.email
        }
    }
    render() {
        return <div className="App">
            <form onSubmit={this.save}>
            Name : <input onChange={e => this.setState({ name: e.target.value })}
                value={this.state.name}
            />
            Password : <input onChange={e => this.setState({ password: e.target.value })}
                value={this.state.password}
            />
            Email : {this.state.email}
            <button type="submit"> Save </button>
            </form>
        </div>
    }

    save = event => {
        event.preventDefault();
        const user = {
            name: this.state.name,
            password : this.state.password,
            email : this.state.email
          };
          console.log("aaa"+JSON.stringify(user))
          axios.post(`localhost:8080/api/user/register`, { user })
            .then(res => {
              console.log(res);
              console.log(res.data);
            })
    }
}

export default RegisterForm;